@import "https://fonts.googleapis.com/css2?family=Inter:wght@400;600&family=Outfit:wght@300;500;700&display=swap";
:root {
  --primary-glow: #00d2ff;
  --secondary-glow: #ff00d2;
  --bg-color: #0d0d12;
  --card-bg: #ffffff0d;
  --card-border: #ffffff1a;
  --text-main: #fff;
  --text-dim: #a0a0b0;
  --accent: #00d2ff;
  --copyright-color: #ffffff80;
  --transition: all .3s cubic-bezier(.4, 0, .2, 1);
}

:root.light {
  --bg-color: #f8f9fa;
  --card-bg: #fffc;
  --card-border: #0000000d;
  --text-main: #1a1a1e;
  --text-dim: #606070;
  --accent: #2563eb;
  --copyright-color: #0009;
}

* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  font-family: Inter, system-ui, -apple-system, sans-serif;
}

body {
  background-color: var(--bg-color);
  color: var(--text-main);
  margin: 0;
  overflow: hidden;
}

canvas {
  z-index: 1;
  width: 100%;
  height: 100%;
  position: fixed;
  inset: 0;
}

h1, h2, h3, .total-count {
  font-family: Outfit, sans-serif;
}

.icon {
  vertical-align: middle;
  opacity: .9;
  background: currentColor;
  width: 20px;
  height: 20px;
  display: inline-block;
  mask-position: center;
  mask-size: contain;
  mask-repeat: no-repeat;
}

.icon.play {
  mask-image: url("play.d2b98249.svg");
}

.icon.shuffle {
  mask-image: url("shuffle.e9e498cf.svg");
}

.icon.megaphone {
  mask-image: url("megaphone.da49dfc8.svg");
}

.icon.record {
  mask-image: url("record.c71c4728.svg");
}

.icon.map {
  mask-image: url("map.eb357c03.svg");
}

.icon.trophy {
  mask-image: url("trophy.50d1ba0a.svg");
}

.icon.bomb {
  mask-image: url("bomb.455f9b17.svg");
}

.icon.sun {
  mask-image: url("sun.ab6ec46d.svg");
}

.icon.moon {
  mask-image: url("moon.3f475bd2.svg");
}

#settings {
  background: var(--card-bg);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid var(--card-border);
  z-index: 1000;
  border-radius: 24px;
  width: 420px;
  max-width: 90vw;
  padding: 24px;
  position: fixed;
  bottom: 2rem;
  left: auto;
  right: 2rem;
  transform: none;
  box-shadow: 0 20px 40px #0006;
}

#settings.hide {
  opacity: 0;
  pointer-events: none;
  transform: translateY(20px)scale(.95);
}

#settings .controls {
  flex-direction: column;
  gap: 14px;
  width: 100%;
  display: flex;
}

#settings .name-section {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

#settings .name-section h3 {
  color: var(--text-dim);
  text-transform: uppercase;
  letter-spacing: .05em;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
}

#settings .name-section textarea {
  border: 1px solid var(--card-border);
  color: #fff;
  resize: none;
  scrollbar-width: thin;
  background: #ffffff1a;
  border-radius: 12px;
  outline: none;
  width: 100%;
  min-height: 52px;
  padding: 10px 12px;
  font-size: 14px;
  line-height: 1.5;
  transition: border-color .3s;
}

#settings .name-section textarea:focus {
  border-color: var(--accent);
  background: #ffffff26;
}

.row {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.row label {
  color: var(--text-dim);
  align-items: center;
  gap: 8px;
  font-size: 13px;
  display: flex;
}

.row select, .row input[type="number"] {
  border: 1px solid var(--card-border);
  color: var(--text-main);
  background: #0003;
  border-radius: 10px;
  width: 100%;
  padding: 8px 12px;
  font-size: 14px;
}

.row select:focus, .row input[type="number"]:focus {
  border-color: var(--accent);
  outline: none;
}

.row.toggle-row {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
}

.row.inline-row {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.row.inline-row select {
  flex: 1;
  width: auto;
  min-width: 150px;
}

.toggle-item {
  flex: 1;
  align-items: center;
  gap: 8px;
  display: flex;
}

.toggle-item label {
  white-space: nowrap;
  margin: 0;
  font-size: 12px;
}

.toggle-item.theme-toggle {
  justify-content: flex-end;
  gap: 10px;
}

input[type="checkbox"] {
  appearance: none;
  cursor: pointer;
  width: 44px;
  height: 24px;
  transition: var(--transition);
  background: #ffffff1a;
  border-radius: 20px;
  position: relative;
}

input[type="checkbox"]:before {
  content: "";
  width: 18px;
  height: 18px;
  transition: var(--transition);
  background: #fff;
  border-radius: 50%;
  position: absolute;
  top: 3px;
  left: 3px;
}

input[type="checkbox"]:checked {
  background: var(--accent);
}

input[type="checkbox"]:checked:before {
  transform: translateX(20px);
}

.actions {
  gap: 8px;
  margin-top: 8px;
  display: flex;
}

.actions button {
  cursor: pointer;
  transition: var(--transition);
  color: var(--text-main);
  background: #ffffff0d;
  border: none;
  border-radius: 12px;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}

.actions button:hover {
  background: #ffffff1a;
  transform: translateY(-2px);
}

.actions button#btnStart {
  background: var(--accent);
  color: #000;
  flex-grow: 1;
  justify-content: center;
  box-shadow: 0 4px 15px #00d2ff4d;
}

.actions button#btnStart:hover {
  box-shadow: 0 8px 25px #00d2ff80;
}

.btn-group {
  background: #0003;
  border-radius: 10px;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 4px;
  padding: 4px;
  display: grid;
}

.btn-group button, .btn-group input {
  height: 32px;
  color: var(--text-dim);
  cursor: pointer;
  transition: var(--transition);
  background: none;
  border: none;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
}

.btn-group button.active, .btn-group input.active {
  background: var(--card-bg);
  color: var(--text-main);
  box-shadow: 0 2px 8px #0003;
}

.theme-toggle {
  justify-content: flex-end;
  align-items: center;
  gap: 12px;
  margin-top: auto;
  display: flex;
}

.copyright {
  text-align: right;
  width: 420px;
  max-width: 90vw;
  color: var(--copyright-color);
  z-index: 999;
  letter-spacing: .02em;
  padding: 8px 12px;
  font-size: 9px;
  line-height: 1.4;
  position: fixed;
  bottom: .5rem;
  right: 2rem;
}

.copyright a {
  color: inherit;
  font-weight: 600;
  text-decoration: none;
}

.copyright a:hover {
  text-decoration: underline;
}

#notice {
  background: var(--bg-color);
  border: 1px solid var(--card-border);
  z-index: 2000;
  border-radius: 32px;
  flex-direction: column;
  width: 480px;
  max-width: 90%;
  padding: 32px;
  animation: .5s cubic-bezier(.16, 1, .3, 1) modalIn;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 30px 60px #0009;
}

#notice h1 {
  margin-top: 0;
  font-size: 28px;
}

#notice .notice-body img {
  border-radius: 16px;
  width: 100%;
}

#notice .notice-action button {
  background: var(--accent);
  color: #000;
  cursor: pointer;
  border: none;
  border-radius: 16px;
  width: 100%;
  height: 52px;
  margin-top: 24px;
  font-weight: 700;
}

@keyframes modalIn {
  from {
    opacity: 0;
    transform: translate(-50%, -40%)scale(.95);
  }

  to {
    opacity: 1;
    transform: translate(-50%, -50%)scale(1);
  }
}

div.toast {
  background: var(--accent);
  color: #000;
  z-index: 3000;
  border-radius: 100px;
  padding: 12px 24px;
  font-weight: 600;
  position: fixed;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 10px 20px #0003;
}

#donate {
  z-index: 1000;
  transition: var(--transition);
  position: fixed;
  bottom: 2rem;
  right: 2rem;
}

#donate.hide {
  opacity: 0;
  visibility: hidden;
}

@media (width <= 768px) {
  #settings {
    border-radius: 32px 32px 0 0;
    flex-direction: column;
    width: 100%;
    padding: 24px;
    bottom: 0;
    left: 0;
  }

  #donate {
    bottom: calc(100% + 20px);
    right: 20px;
  }
}
